.home{
    background: rgb(0,74,173);
background: linear-gradient(90deg, #1D3557, #457B9D);
    display: flex;
    flex-direction: column;
    height: 100vh;
    scroll-snap-align: center;
    overflow-y: scroll;
    scroll-snap-type: y proximity;
}
.home h1{
    text-align: left;
    position: absolute;
    top: 45%;
    left: 50px;
    font-size: 75px;
    transition: ease-in;
}

.home h1 span{
    color: #A8DADC;
    font-size: 100px;
}
.home h2{
    font-size: 40px;
    position: absolute;
    bottom: 5%;
    right: 5%;
    color: #ffffff;
}
.home .ima span{
    color: #A8DADC;
}

.anim{
    animation-duration: 0.5s;
    animation-name: run;
}
.anim-r{
    animation-duration: 0.5s;
    animation-name: run-r;
}

.home p{
    display: none;
    position: absolute;
    top: 65%;
    text-align: left;
    left: 65px;
    color: rgba(255, 255, 255, 0.521);
    font-size: 22px;
    padding: 10px;
}
.home img{
    width: 25%;
    position: absolute;
    right: 100px;
    top: 20%;
    
}

@media screen and (max-width: 769px){

    .home{
        display: flex;
        background-color: #0b5a5d;
    }
    .home h1{
        position: absolute;
        text-align: left;
        left: 0px;
        right: 0px;
        bottom: 0px;
        font-size: 30px;
        text-align: center;
        
    }
    
    .home h1 span{
        color: #A8DADC;
        font-size: 40px;
    }
    
    .home p{
        position: absolute;
        text-align: center;
        left: 0;
        right: 0;
        bottom: 0px;
        color: rgba(255, 255, 255, 0.678);
        font-size: 15px;
    }
    .home img{
        width: 35%;
        position: absolute;
        top: 11%;
        right: 30px;
    }
}

@keyframes run {
    0% {
        opacity: 0;
      left: -300px;
    }
    100% {
        opacity: 1;
      left: 50px;
    }
  }

  @keyframes run-r {
    0% {
        opacity: 0;
      right: -300px;
    }
    100% {
        opacity: 1;
      right: 100px;
    }
  }