.skills{
    width: 100%;
    height: 100vh;
    background: linear-gradient(90deg, #1D3557, #457B9D);
    padding: 50px;
    display: flex;
    flex-direction: column;
    scroll-snap-align: center;
   overflow-y: scroll;
    
}
.skills h1{
    display: flex;
    position: relative;
    align-self: flex-start;
    left: 50px;
    margin-bottom: 30px;
    font-size: 51px;
    /* border-bottom:10px solid #A8DADC;
    border-width: 5px; */
    /* border-radius : 10px; */
    padding: 5px;
    color: #F1FAEE;
}
.skillBar{
    height: 100%;
    align-self: center;
    justify-self: center;
    width: 60%;
    margin-top: 10%;
}

.anim{
    animation-duration: 0.5s;
    animation-name: run;
}
.anim-r{
    animation-duration: 0.5s;
    animation-name: run-r;
}

@media screen and (max-width: 769px){
    .skills{
        width: 100%;
        height: fit-content;
        background: linear-gradient(90deg, #1D3557, #457B9D);
        padding: 5px;
        display: flex;
        flex-direction: column;
        padding-bottom: 100px;
       
        
    }
    .skills h1{
        display: flex;
        align-self: center;
        margin-bottom: 20px;
        font-size: 26px;
        left: 0;
        /* border-bottom:10px solid #A8DADC;
        border-width: 5px; */
        /* border-radius : 10px; */
        padding: 5px;
        color: #F1FAEE;
    }
    .skillBar{
        height: 100%;
        align-self: center;
        justify-self: center;
        width: 80%;
    }
}

@keyframes run {
    0% {
        opacity: 0;
      left: -300px;
    }
    100% {
        opacity: 1;
      left: 50px;
    }
  }

  @keyframes run-r {
    0% {
        opacity: 0;
      right: -300px;
    }
    100% {
        opacity: 1;
      right: 100px;
    }
  }