*{
    
}
nav{
display: flex;

  align-items: center;
  justify-content: space-between;
  background: #1D3557;
  padding: 10px 50px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
  position: relative;
  top: 0;
  left: 0;
  z-index: 999;
}



nav .logo img{
    width: 50px;
    height: 70px;
    transition: 0.3s;
}
nav #navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    
}

#navbar li {
    list-style: none;
    padding: 0 20px;
    position: relative;
  }
  
  #navbar li a {
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: #F1FAEE;
    transition: 0.3s ease-in-out;
}
 #navbar li a:hover,
  #navbar li a.active {
    color: #A8DADC;
  }
  
  #navbar li a:hover::after,
  #navbar li a.active::after {
    content: "";
    width: 30%;
    height: 2px;
    background: #A8DADC;
    position: absolute;
    bottom: -4px;
    left: 20px;
  }
  #mobile{   
   display: none;
  }

  #mobile i{
    color: #A8DADC;
  }


  @media screen and (max-width: 769px){

    nav{
      display: flex;
      height: 80px;
      padding: 5px 30px;
      z-index: 50;
      transition: 0.3s ease-in-out;
      }

    nav #navbar {
      display: none;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      position: fixed;
      top: 80px;
      right: -300px;
      width: 300px;
      height: 100vh;
      background: #1D3557;
      box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
      padding: 40px 0 0 10px;
      /* transition: 0.3s ease-in-out; */
      animation: smooth-out 1s ease;
    }

  
    nav .logo img{
      width: 40px;
      height: 60px;
  }
    #navbar.active {
      animation: smooth-in 1s ease;
      display: flex;
      right: 0px;
      transition: 0.3s ease-in-out;
    }
  
    #navbar li {
      margin-bottom: 25px;
    }
  
    #navbar li a{
      color: antiquewhite;
    }


    #mobile {
      display: block;
      
    }
  
    #mobile i {
      font-size: 24px;
      cursor: pointer;
      color: #ffffff;
      transition: 0.3s ease-in-out;
      margin-right: 20px;
    }
  }

  @keyframes smooth-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@keyframes smooth-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}