*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
.appcontainer{
  overflow-y: auto;
  overscroll-behavior-y: contain;
  scroll-snap-type: y mandatory;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.headroom{
  position: relative;
  z-index: 999;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.inani{
  animation-duration: 1s;
  animation-name: in;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes in{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

::-webkit-scrollbar {
  background: #457B9D;
  width: 12px;
}
::-webkit-scrollbar-thumb{
  background: linear-gradient(0deg, #1D3557, #457b9d00);
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover{
  
}
