.top{
    display: flex;
    flex-direction: column;
    height:100vh;
    background: rgb(0,74,173);
    background: linear-gradient(90deg, #1D3557, #457B9D);
    padding-top: 2%;
    scroll-snap-align: center;
   overflow-y: scroll;
   scroll-snap-type: y proximity;
}
.swiper{
  width: 60%;  
  padding: 5px;
}

.top h1{
    display: flex;
    position: relative;
    align-self: flex-start;
    left: 50px;
    margin-bottom: 30px;
    font-size: 51px;
    /* border-bottom:10px solid #A8DADC;
    border-width: 5px; */
    /* border-radius : 10px; */
    padding: 5px;
    color: #F1FAEE;
}
.top .project-list{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.anim{
    animation-duration: 0.5s;
    animation-name: run;
}


@media screen and (max-width: 769px){
    .top{
        display: flex;
        justify-content: center;
        height: fit-content;
        align-items: center;
        
    }
    .top h1{
        display: flex;
        align-self: center;
        margin-bottom: 30px;
        font-size: 26px;
        /* border-bottom:10px solid #A8DADC;
        border-width: 5px;
        border-radius : 10px; */
        padding: 5px;
        left: 0;
        color: #F1FAEE;
        
    }
    .swiper{
        width: 95%;  
        padding: 5px;
        
      }
    .top .project-list{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
        
    }
}

@keyframes run {
    0% {
        opacity: 0;
      left: -300px;
    }
    100% {
        opacity: 1;
      left: 50px;
    }
  }