.main{
    background: rgb(0,74,173);
    background: linear-gradient(90deg, #1D3557, #457B9D);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    scroll-snap-align: center;
   overflow-y: scroll;
   scroll-snap-type: y proximity;
}
.main h1{
    font-size: 54px;
    color: #F1FAEE;
    /* border-bottom:10px solid #A8DADC;
    border-width: 5px; */
    /* border-radius : 10px; */
    display: flex;
    position: relative;
    align-self: flex-start;
    left: 50px;
    margin-bottom: 30px;
    font-size: 51px;
    padding: 5px;
}
.anim{
    animation-duration: 0.5s;
    animation-name: run;
}


.main #mail a{
    margin-top: 50px;
    text-decoration: none;
    color: #F1FAEE;
}
.main img{
    width: 200px;
    height: 280px;
    opacity: 0.9;
    margin: 50px;
}

.sociallinks ul{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}

.sociallinks ul li{
    margin: 30px;
    list-style: none;
}
.sociallinks ul li svg{
    width: 40px;
    height: 40px;
    color: azure;
    
}

.sociallinks ul li a :hover{
    overflow:hidden;
    transform: scale(1.15);
    color: rgb(30, 192, 192);
}

@media screen and (max-width: 769px){
    .main h1{
        display: flex;
        align-self: center;
        margin-bottom: 30px;
        font-size: 26px;
        /* border-bottom:10px solid #A8DADC;
        border-width: 5px;
        border-radius : 10px; */
        padding: 5px;
        left: 0;
        color: #F1FAEE;
    }
}


@keyframes run {
    0% {
        opacity: 0;
      left: -300px;
    }
    100% {
        opacity: 1;
      left: 50px;
    }
  }
