.card{
    border: 5px;
    border-radius: 10px;
    height: 70vh;
    width: 95%;
    background: '#457B9D';
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 50px 20px;
    display: flex;
    flex-direction: column;
    scroll-snap-align: center;
   overflow-y: scroll;
    
}
.card .title{
    text-align: center;
    font-size: 25px;
    color: aliceblue;
    
}

.card img{
    border: 5px;
    border-radius: 10px;
    width: 100%;
    height: 70%;
    object-fit:cover;
}
.card p{
    color: rgba(255, 255, 255, 0.735); 
}
.card h2{
    margin-top: 20px;
    color: #E63946;
}
.card a{
    text-decoration: none;
}
.card .card-cont{
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


@media screen and (max-width: 769px){
    .card{
        border: 5px;
        border-radius: 20px;
        height: 60vh;
        width: 90%;
        margin-bottom: 20px;
        display: flex;
        
        
        
    }
    .card img{
        border: 5px;
        border-radius: 20px;
        width: 100%;
        height: 100%;
    }
    .card .title{

    }

    .card p{
        
    }
}
